import React from 'react';
import classes from '../Navs.module.css';
import { FcAbout } from 'react-icons/fc';
import Paragraph from '../../Paragraph/Paragraph'

const AboutUs = () => {
    return (
        <div>
            <h2 className={classes.Icon}><FcAbout /></h2>
            <Paragraph />
        </div>
    );
};

export default AboutUs;