import React from 'react';
import classes from '../Navs.module.css';
import { AiOutlineUser } from 'react-icons/ai';
import Paragraph from '../../Paragraph/Paragraph';

const Users = (props) => {
    const QueryParams = new URLSearchParams(props.location.search);
    const UsersSearch = [...QueryParams.entries()];

    const Search = UsersSearch.map((Search, index) =>
        <div className='text-center mb-4 text-uppercase'>
            <label key={index}> {Search[0]} :</label>
            <strong>&nbsp;{Search[1]}</strong>
        </div>
    )

    return (
        <div>
            <h1 className={[classes.Icon, 'mb-4 text-center'].join(' ')}><AiOutlineUser /></h1>
            {Search}
            <Paragraph />
        </div>
    );
};

export default Users;