import React from 'react';
import classes from '../Navs.module.css';
import { AiOutlineHome } from 'react-icons/ai';
import Paragraph from '../../Paragraph/Paragraph';

const Home = () => {
    return (
        <div>
            <h2 className={classes.Icon}><AiOutlineHome /></h2>
            <Paragraph />
        </div>
    );
};

export default Home;