import React from 'react';
import classes from '../Navs.module.css';
import { FcFeedback } from 'react-icons/fc';
import Paragraph from '../../Paragraph/Paragraph';

const FeedBack = () => {
    return (
        <div>
            <h2 className={classes.Icon}><FcFeedback /></h2>
            <Paragraph />
        </div>
    );
};

export default FeedBack;