import React, { Fragment } from 'react';
import classes from './Content.module.css'
import { Switch, Route, withRouter, Redirect } from "react-router-dom";
import AboutUs from '../Navs/AboutUs/AboutUs';
import ContactUs from '../Navs/ContactUs/ContactUs';
import CustomerService from '../Navs/CustomerService/CustomerService';
import Features from '../Navs/Features/Features';
import FeedBack from '../Navs/FeedBack/FeedBack';
import Home from '../Navs/Home/Home';
import LogIn from '../Navs/LogIn/LogIn';
import Pricing from '../Navs/Pricing/Pricing';
import Register from '../Navs/Register/Register';
import Users from '../Navs/Users/Users';
import NotFound from '../Navs/NotFound/NotFound';

const Content = props => {
    return (
        <Fragment>
            <div className={classes.Content}>
                <Switch>
                    <Redirect strict from='/home' to='/' />
                    <Redirect strict from='/aboutus' to='/about-us' />
                    <Redirect strict from='/contactus' to='/contact-us' />
                    <Redirect strict from='/customerservice' to='/customer-service' />
                    <Route exact path="/" component={Home} />
                    <Route path="/features" component={Features} />
                    <Route path="/pricing" component={Pricing} />
                    <Route path="/about-us" component={AboutUs} />
                    <Route path="/contact-us" component={ContactUs} />
                    <Route path="/customer-service" component={CustomerService} />
                    <Route path="/feedback" component={FeedBack} />
                    <Route path="/login" component={LogIn} />
                    <Route path="/register" component={Register} />
                    <Route path="/users" component={Users} />
                    <Route path='*' component={NotFound} />
                </Switch>
            </div>
        </Fragment>
    );
}

export default withRouter(Content);